<template>
   <div class="add-btn-wrapp">
      <el-tooltip class="item" effect="dark" content="Предложить товар" placement="top">
         <el-button v-show="!isFormOpen" type="primary" icon="el-icon-plus" circle @click="isFormOpen = true"></el-button>
      </el-tooltip>
      <transition name="el-fade-in">
         <div v-show="isFormOpen" class="add-form--wrapp">
            <el-card shadow="always">
               <h2>Предложите товар</h2>
               <hr>

               <el-form ref="form" :model="arr_formData" label-width="120px" size="mini">
                  <transition-group name="el-fade-in">
                     <div class="form-tab" v-show="currTab == 0" :key="0">
                        <el-form-item label="Ссылка на товар">
                           <el-input v-model="formData.link"></el-input>
                        </el-form-item>
                     </div>
                     <div class="form-tab" v-show="currTab == 1" :key="1">
                        <el-form-item label="Название товара">
                           <el-input v-model="formData.name"></el-input>
                        </el-form-item>
                        <el-form-item label="Цена">
                           <el-input-number v-model="formData.price" :precision="2" :step="0.1"></el-input-number>
                        </el-form-item>
                     </div>
                     <div class="form-tab" v-show="currTab == 2" :key="2">
                        <el-form-item label="Ссылка на фото">
                           <el-input v-model="formData.img_url"></el-input>
                        </el-form-item>
                     </div>
                  </transition-group>

               </el-form>

               <div class="add-form--footer">
                  <el-steps :active="currTab">
                     <el-step title="Шаг 1" description="Ссылка на товар"></el-step>
                     <el-step title="Шаг 2" description="Название, цена"></el-step>
                     <el-step title="Шаг 3" description="Фото"></el-step>
                  </el-steps>
                  <el-button type="primary" size="small" @click="currTab++" plain>Продолжить <i class="el-icon-arrow-right"></i></el-button>
               </div>
            </el-card>
         </div>
      </transition>
   </div>

</template>

<script>
   export default {
      name: 'addprod-btn',
      data() {
         return {
            currTab: 0,
            isFormOpen: false,
            arr_formData: {},
            formData: {
               link: '',
               name: '',
               price: 0.00,
               img_url: ''
            }
         }
      }
   }

</script>

<style lang="scss">
   @import "@/style/_variables.scss";

   .add-btn-wrapp {
      position: fixed;
      bottom: $base-padding*2;
      left: $base-padding*2;
      z-index: 1000;
   }

   .add-form {

      &--wrapp {
         width: 35vw;
      }

      &--footer {
         padding-top: $base-padding;

         .el-button {
            width: 100%;
            margin-top: $base-padding;
         }
      }
   }

</style>
