<template>
   <transition name="el-fade-in-linear" appear>
      <el-card :body-style="{ padding: '0px' }" class="prod" shadow="hover">

         <div class="top">
            <div>
               <img v-if="!!data.fav_url" :src="data.fav_url" class="fav-icon"><span>{{domain}}</span>
            </div>
            <div>
               <el-tooltip class="item" effect="dark" :content="catss" placement="right">
                  <i class="text-right el-icon-info"></i>
               </el-tooltip>
            </div>
         </div>

         <div class="fade" v-loading="loading">
            <el-image class="image" :src="data.img_url" fit="cover" v-on:load="loading = false" v-on:error="loading = false">
               <div slot="error" class="image-slot">
                  <i class="el-icon-picture-outline"></i>
               </div>
            </el-image>
            <!--        <img v-on:load="loading = false" :src="data.img_url" class="image">-->
         </div>

         <div class="title-wrapp">
            <p class="title" :style="{fontSize: titleSize}">{{data.title}}</p>
         </div>

         <div class="bottom clearfix">
            <div style="float: left"><span class="price">{{data.price}} ₴</span> </div>
            <product-data :info="data.info"></product-data>
            <a :href="data.link" target="_blank">
               <el-button type="primary" plain>Посмотреть на сайте <i class="el-icon-arrow-right"></i></el-button>
            </a>
         </div>
      </el-card>
   </transition>
</template>

<script>
   import productData from '@/components/productData.vue';
   export default {
      name: 'productCard',
      props: ['data'],
      data() {
         return {
            titleSize: '1.4em',
            loading: true
         }
      },
      methods: {

      },
      computed: {
         domain() {
            return this.$props['data'].link.match(/([a-z0-9A-Z]\.)*[a-z0-9-]+\.([a-z0-9]{2,24})+(\.co\.([a-z0-9]{2,24})|\.([a-z0-9]{2,24}))*/g)[0] || false;
         },
         catss() {
            return this.$props['data'].categories.join(', ');
         }
      },
      beforeMount() {
         this.titleSize = (this.$props['data'].title.length > 75) ? '1.15em' : '1.2em';
      },
      components: {
         productData
      }
   }

</script>

<style lang="scss">
   @import "@/style/_variables.scss";

   .el-card.prod {
      height: $card-height;

      .image-slot {
         padding-top: 40%;
         height: 100%;
         text-align: center;
         
         i {
            font-size: 5em;
            color: $border-color;
         }
      }

      .title,
      .el-button,
      .top,
      .bottom span::after,
      .fade::after {
         @extend %trans;
      }

      .el-card__body {
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         height: inherit;
      }

      .fade {
         position: relative;
         width: 100%;
         height: $card-img-height;


         .image {
            width: inherit;
            height: inherit;
            display: block;
            object-fit: cover;
         }

         &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            display: block;
            width: 100%;
            height: 100%;
            background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.1), transparent);
            background-position: center;
            background-repeat: no-repeat;
         }

      }

      &:hover {
         .fade::after {
            height: 30%;
            top: 70%;
         }

         .el-button,
         .top {
            background-color: $--color-primary;
            opacity: 1;
            color: white;
         }


      }


      .title-wrapp {
         height: $card-height - $card-img-height - 65px;
         padding-top: 5px;
      }

      .title {
         margin: 0;
         line-height: 110%;
      }

      .title,
      .bottom .price {
         padding: 0 10px;
      }

      .el-button {
         width: 100%;
         border-radius: 0 0 $radius $radius;
         background-color: white;
         border: none;

         &:hover span::after {
            transform: translateY(0px);
            opacity: 1;
         }
      }

      .bottom {
         position: relative;
         bottom: 0;
         z-index: 100;
         height: 77px;

         .price {
            font-size: 1.3em;
            line-height: 1.3;
            padding-top: 5px;
            font-weight: bold;
            color: $--color-primary;
         }

         span {
            display: inline-block;

            &::after {
               content: '';
               display: block;
               margin: 0 auto;
               width: 100%;
               opacity: 0;
               height: 2px;
               border-bottom: 1px dashed $grey-white;
               transform: translateY(10px);
            }

         }


      }

      .top {
         width: 100%;
         padding: 6px 10px;
         color: $--color-primary;
         line-height: 30px;
         display: flex;
         justify-content: space-between;
         opacity: .8;

         span {
            font-weight: bold;
         }

         .fav-icon {
            height: 15px;
            width: 15px;
            margin: 0 5px 0 0;
            transform: translateY(2px);
         }
      }
   }

</style>
