<template>
   <section class="content">
      <el-row>
         <el-col class="mb">
            <el-card class="main-form" shadow="never">
               <el-row>
                  <el-col :span="20">
                     <h2 class="section-name__inner">Подбор подарка</h2>
                  </el-col>
                  <el-col :span="4" v-if="isFormOpen">
                     <el-button class="b-r sort" icon="el-icon-arrow-up" @click="isFormOpen = false" size="small"><span class="hidden-xs-only">Свернуть</span></el-button>
                  </el-col>
               </el-row>
               <hr>
               <el-collapse-transition>
                  <el-form v-if="isFormOpen" label-position="top">
                     <div class="wrapp-q" v-for="quest in questionList" :key="quest.id">

                        <el-form-item v-if="quest.type == 'radio'" :prop="quest.id" :label="quest.Q">

                           <el-radio-group v-model="quest.data">
                              <el-radio v-for="ans in quest.A" :key="ans.id" :label="ans.a" tabindex="1">{{ans.a}}<span v-if="!!ans.hint" class="hint">
                                    ({{ans.hint}})
                                 </span>
                              </el-radio>
                           </el-radio-group>

                        </el-form-item>
                        <el-form-item v-else-if="quest.type == 'checkbox'" :prop="quest.id" :label="quest.Q">

                           <el-row v-if="!!quest.search" style="margin-bottom: 10px" :gutter="5">

                              <el-col :xl="22" :lg="20" :md="20" :sm="19" :xs="19">
                                 <el-input type="text" :placeholder="quest.data.searchPlaceholder" class="b-r" v-model="quest.data.text" clearable></el-input>
                              </el-col>
                              <el-col :xl="2" :lg="4" :md="4" :sm="5" :xs="5">
                                 <el-tooltip class="item" effect="dark" :content="'Сортировать '+quest.data.sortHint" placement="top">
                                    <el-button @click="sortingList(quest.data)" class="b-r sort" icon="el-icon-sort"><span class="hidden-xs-only">{{quest.data.sortHint}}</span></el-button>
                                 </el-tooltip>
                              </el-col>

                           </el-row>
                           <template v-if="!!quest.data.text">
                              <div v-if="quest.data.countMatch && quest.data.text.trim() !== ''" style="margin-top: -5px">Совпадений <b>{{quest.data.countMatch}}</b></div>
                              <div v-else-if="quest.data.countMatch == 0 && quest.data.text.trim() !== ''" style="margin-top: -5px">Совпадений не найдено</div>
                           </template>

                           <el-row v-loading="quest.loading" v-if="quest.grouped" type="flex" class="check-grid" style="min-height: 100px">

                              <el-col v-for="(ans, i) in alphabetCheckList(quest)" :key="'check'+i" :lg="8" :xs="24" class="mt">
                                 <h3 class="letter">{{ans.letter}}</h3>
                                 <el-checkbox-group v-model="quest.data.answer">
                                    <el-checkbox v-for="(check, a) in ans.list" :key="'check'+i+'_'+a" :label="check.word" :name="check.word"></el-checkbox>
                                 </el-checkbox-group>
                              </el-col>

                           </el-row>
                           <el-row v-else-if="!quest.grouped" v-loading="quest.loading" :key="quest.id" style="min-height: 100px">
                              <el-checkbox-group v-model="quest.data.answer">
                                 <el-checkbox v-for="(ans, i) in alphabetCheckList(quest)" :key="'check'+i" :label="ans.a" :name="ans.a"></el-checkbox>
                              </el-checkbox-group>
                           </el-row>
                        </el-form-item>
                     </div>
                     <el-form-item class="no-b">
                        <el-button type="primary" @click="prepareData">Подобрать</el-button>
                     </el-form-item>
                  </el-form>
                  <div v-else>
                     <el-button type="primary" style="width: 100%" @click="isFormOpen = true">Начать</el-button>
                  </div>
               </el-collapse-transition>
            </el-card>
         </el-col>
      </el-row>
   </section>
</template>

<script>
   import api from '@/services/postService.js';
   import 'element-ui/lib/theme-chalk/display.css';


   export default {
      name: 'formblock',
      data() {
         return {
            isFormOpen: false,
            loading: false,
            questionList: [{
                  id: 'q1',
                  Q: "Кому предназначен подарок?",
                  type: 'radio',
                  data: '',
                  A: [{
                        id: 'q1a1',
                        a: "Женщине"
                     },
                     {
                        id: 'q1a2',
                        a: "Мужчине"
                     }
                  ]
               },
               {
                  id: 'q2',
                  Q: "Какой повод для подарка?",
                  type: 'radio',
                  data: '',
                  A: [{
                        id: 'q2a1',
                        a: "День Рождения"
                     },
                     {
                        id: 'q2a2',
                        a: "Новый Год"
                     },
                     {
                        id: 'q2a3',
                        a: "Мужской день"
                     },
                     {
                        id: 'q2a4',
                        a: "Мальчишник"
                     },
                     {
                        id: 'q2a5',
                        a: "Свадьба"
                     },
                     {
                        id: 'q2a6',
                        a: "Без повода"
                     }
                  ]
               },
               {
                  id: 'q3',
                  Q: "Какого он возраста?",
                  type: 'radio',
                  data: '',
                  A: [{
                        id: 'q3a1',
                        a: "Дошкольного",
                        hint: "до 6 лет"
                     },
                     {
                        id: 'q3a2',
                        a: "Школьного",
                        hint: "6 - 12 лет"
                     },
                     {
                        id: 'q3a3',
                        a: "Подросткового",
                        hint: "13 - 19 лет"
                     },
                     {
                        id: 'q3a4',
                        a: "Молодого",
                        hint: "20 - 35 лет"
                     },
                     {
                        id: 'q3a5',
                        a: "Взрослого",
                        hint: "36 - 60 лет"
                     },
                     {
                        id: 'q3a6',
                        a: "Пожилого",
                        hint: "от 60 лет"
                     }
                  ]
               },
               {
                  id: 'q4',
                  Q: "Что ему нравится?",
                  type: 'checkbox',
                  search: true,
                  grouped: true,
                  loading: true,
                  data: {
                     text: '',
                     lastText: false,
                     isSortUp: false,
                     searchPlaceholder: 'Поиск по увлечениям',
                     sortHint: 'А-Я',
                     countMatch: false,
                     isListFormatted: false,
                     sortingText: {
                        up: 'Я-А',
                        down: 'А-Я'
                     },
                     answer: [],
                     actualList: []
                  },
                  A: [{
                     a: 'Автомобили'
                  }, {
                     a: 'Велосипед'
                  }, {
                     a: 'Дайвинг'
                  }, {
                     a: 'Аэробика'
                  }, {
                     a: 'Бадминтон'
                  }, {
                     a: 'Бег'
                  }, {
                     a: 'Бильярд'
                  }, {
                     a: 'Блоггерство'
                  }, {
                     a: 'Вязание'
                  }, {
                     a: 'Выжигание по дереву'
                  }, {
                     a: 'Головоломки'
                  }, {
                     a: 'Гольф'
                  }, {
                     a: 'Граффити'
                  }, {
                     a: 'Дартс'
                  }, {
                     a: 'Дизайн'
                  }, {
                     a: 'Живопись'
                  }, {
                     a: 'Животные'
                  }, {
                     a: 'Игрушки'
                  }, {
                     a: 'Иностранные языки'
                  }, {
                     a: 'Компьютерные игры'
                  }, {
                     a: 'Компьютеры'
                  }, {
                     a: 'Картинг'
                  }, {
                     a: 'Кулинария'
                  }, {
                     a: 'Мемы'
                  }, {
                     a: 'Мультфильмы'
                  }, {
                     a: 'Музыка'
                  }, {
                     a: 'Моделирование'
                  }, {
                     a: 'Пазлы'
                  }, {
                     a: 'Плавание'
                  }, {
                     a: 'Рисование'
                  }, {
                     a: 'Робототехника'
                  }, {
                     a: 'Рукоделие'
                  }, {
                     a: 'Рыбалка'
                  }, {
                     a: 'Сериалы'
                  }, {
                     a: 'Спорт'
                  }, {
                     a: 'Фильмы'
                  }, {
                     a: 'Фотография'
                  }, {
                     a: 'Пение'
                  }]
               },
               {
                  id: 'q5',
                  Q: "Какой подарок ему подойдет?",
                  type: 'checkbox',
                  data: {
                     answer: []
                  },
                  A: [{
                        a: "Полезный/Практичный"
                     },
                     {
                        a: "На каждый день"
                     },
                     {
                        a: "Шуточный"
                     },
                     {
                        a: "На память"
                     },
                     {
                        a: "Ценный"
                     },
                     {
                        a: "Интелектуальный"
                     },
                  ]
               },
               {
                  id: 'q6',
                  Q: "Доставка из:",
                  type: 'checkbox',
                  search: true,
                  grouped: false,
                  loading: true,
                  data: {
                     text: '',
                     lastText: false,
                     isSortUp: false,
                     searchPlaceholder: 'Поиск по странам',
                     sortHint: 'А-Я',
                     countMatch: false,
                     isListFormatted: false,
                     sortingText: {
                        up: 'Я-А',
                        down: 'А-Я'
                     },
                     answer: [],
                     actualList: []
                  },
                  A: [{
                        a: "Украина"
                     },
                     {
                        a: "Китай"
                     },
                     {
                        a: "США"
                     },
                     {
                        a: "Европа"
                     }
                  ]
               }

            ]
         }
      },
      methods: {
         returnFormData() {
            return this.questionList.map((el) => {
               if (typeof el.data == 'string') return {
                  id: el.id,
                  data: el.data
               };
               else if (typeof el.data == 'object') return {
                  id: el.id,
                  data: el.data.answer
               }
            });
         },
         // Отправка данных на сервер
         prepareData() {
            let allData = this.returnFormData();
            api.findPresents(allData)
               .then((res) => {
                  console.log(res)
               }).catch((err) => {
                  this.$alert(err, "Ошибка", {
                     type: 'error',
                     confirmButtonText: 'OK'
                  })
               });
         },
         sortingList(data) {
            data.isSortUp = !data.isSortUp;
            data.isListFormatted = false;
            data.sortHint = (data.isSortUp) ? data.sortingText.up : data.sortingText.down;
         },
         // Возвращает отсортированный по алфавиту массив
         returnSortedList(arr, isUp) {
            return isUp ? arr.sort((el1, el2) => (el1.a.toLowerCase() < el2.a.toLowerCase()) ? 1 : -1) : arr.sort((el1, el2) => (el1.a.toLowerCase() < el2.a.toLowerCase()) ? -1 : 1);
         },
         returnGroupedCheckList(arr) {
            let newArr = [];
            let lastIndex = 0;
            for (let i = 0; i < arr.length; i++) {
               let lastItemArr = newArr[lastIndex]; // Предыдущий элемент нового массива
               let word = arr[i].a; // Слово
               let Ltr = word[0].toLowerCase(); // Первая буква в нижнем регистре

               // Если массив не пустой
               if (newArr.length > 0) {
                  // Если первая буква последнего элемента 
                  // не совпадает с текущей, то добавляется
                  // в новый объект вместе со словом
                  // и обновляется индекс для следующего элемента
                  if (lastItemArr.letter !== Ltr) {
                     newArr.push({
                        letter: Ltr,
                        list: [{
                           word
                        }]
                     });
                     lastIndex++;
                  } else lastItemArr.list.push({
                     word
                  }); // Если первая буква сопадает, то в текущий объект добавляется слово, индекс не меняется, вдруг следующее слово тоже совпадет с текущим
               } else {
                  // Если массив пустой, в него добавляется первый элемент с буквой и словом
                  newArr.push({
                     letter: Ltr,
                     list: [{
                        word
                     }]
                  });
               }
            }
            return newArr;
         },
         // Поиск по тегам
         returnSearch(arr, search) {
            return arr.filter(el => el.a.toLowerCase().indexOf(search.trim().toLowerCase()) > -1);
         },
         alphabetCheckList(quest) {
            let arr = quest.A;
            let data = quest.data;
            let isGrouped = quest.grouped;
            if (!data.isListFormatted || data.text !== data.lastText) {
               quest.loading = true;
               if (!!data.text && data.text !== '') arr = this.returnSearch(arr, data.text);
               // Сортировка по алфавиту
               arr = this.returnSortedList(arr, data.isSortUp);

               if (data.text !== '') data.countMatch = arr.length;
               else data.countMatch = false;

               data.isListFormatted = true;

               data.lastText = data.text;
               let answer = (isGrouped) ? this.returnGroupedCheckList(arr) : arr;
               quest.loading = false;
               return data.actualList = answer
            } else return data.actualList;
         }
      },
      created() {
         
         
      }, 
      mounted() {
         
         if (localStorage.getItem('isFormOpen')) {
            localStorage.setItem('isFormOpen', this.isFormOpen) 
         } else this.isFormOpen = localStorage.getItem('isFormOpen')
         
      }
   }

</script>

<style lang="scss">
   @import "@/style/_variables.scss";

   .el-card hr {
      border: none;
      background-color: $border-color;
      color: $border-color;
      height: 2px;
      margin-bottom: 20px;
   }
   
   .el-col.mt {
      margin-top: .5em;
   }
   .el-card.main-form {
      padding: $base-padding;

      .hint {
         font-size: .9em;
         color: lighten($--color-primary, 55%);
      }

      .el-form-item {
         padding-bottom: 10px;
         border-bottom: 1px solid $border-color;
         margin-bottom: 17px;

         &.no-b {
            border: none;
            margin: 0;
            padding: 0;

            .el-button {
               margin-top: 5px;
               width: 100%;
            }
         }

         .b-r .el-input__inner,
         .b-r {
            border-radius: $radius;
         }
      }
         button.el-button.el-button--default.sort {
            width: 100%;
            border-radius: $radius;
         }


      .el-radio,
      .el-checkbox {
         display: block;
         margin-bottom: 8px;
      }

      .el-radio__label,
      .el-checkbox__label {
         font-size: 1.1em;


      }

      .el-checkbox {
         line-height: 1;


      }

      .el-form-item__label {
         font-size: 1em;
         line-height: 1.9em;
         font-weight: bold;
      }
   }

   h3.letter {
      font-size: 1em;
      color: lighten($--color-primary, 55%);
      text-transform: uppercase;
      line-height: 1;
      margin-bottom: .8em;
   }

   h2.section-name__inner {
      font-size: 1.6em;
      text-align: left;
      margin: 0 0 10px 0;
   }

   .el-row--flex.check-grid {
      flex-wrap: wrap;
      flex-direction: row;
      /*      max-height: 700px;*/

   }

</style>
