<template>
   <div v-show="!!info" style="display: inline-block; float: right; padding: 8px 10px 0 0;">
      <el-tooltip v-for="(ico, i) in info" :key="i"  class="item" effect="dark" :content="tips[ico]" placement="top">
         <div class="icon-data" :class="'ico-'+ico"></div>
      </el-tooltip>
   </div>
</template>
<script>
   export default {
      name: 'productdata',
      props: ['info'],
      data() {
         return {
            tips: {
               'CH': 'Доставка с Китая',
               'UA': 'Доставка с Украины',
               'np': 'Доставка Новой почтой',
               'up': 'Доставка Укрпочтой',
               'free': 'Бесплатная доставка',
            }
         }
      }
   }

</script>
<style lang="scss">
   @import "@/style/_variables.scss";

   .icon-data {
      @extend %trans;

      width: 17px;
      height: 17px;
      display: inline-block;
      filter: grayscale(1);
      margin-left: 8px;
      opacity: .6;
      background: url('../img/icons/icons-prod-info.svg') no-repeat;
      background-position: 0 20px;
      background-size: auto 100%;

      &.ico-CH {
         background-position: 0;
      }

      &.ico-UA {
         background-position: -27px 0;
      }

      &.ico-np {
         background-position: -53px 0;
      }

      &.ico-up {
         background-position: -78px 0;
      }

      &.ico-free {
         width: 24px;
         background-position: -101px 0;
      }
   }

   .el-card.prod:hover .icon-data {
      filter: none;
      opacity: 1;
   }

</style>
