<template>
   <section class="content">
      <el-row>
         <el-col class="mb">
            <el-card class="search" shadow="never">
            
               <el-input :placeholder="searchPlaceholder" v-model="inputSearch">
            
                  <el-select v-model="selectedOption" slot="prepend" value="123" name="findOn" @change="changePlaceholder">
                     <el-option v-for="(opt, i) in options" :label="opt.name" :value="opt.value" :key="i" :name="opt.value"></el-option>
                  </el-select>
            
                  <el-button slot="append" icon="el-icon-search"></el-button>
            
               </el-input>
            
            </el-card>
         </el-col>
      </el-row>
   </section>
</template>

<script>
   export default {
      name: 'searchblock',
      data() {
         return {
            selectedOption: 'all',
            inputSearch: '',
            searchPlaceholder: 'Поиск по сайту',
            options: [{
                  name: 'Все',
                  placeholder: 'сайту',
                  value: 'all'
               }, {
                  name: "Товары",
                  placeholder: "товарам",
                  value: "products"
               },
               {
                  name: "Категории",
                  placeholder: "категориям",
                  value: "category"
               }
            ]
         }
      },
      methods: {
         changePlaceholder() {
            let val = this.selectedOption;
            this.searchPlaceholder = 'Поиск по ' + this.options.filter(el => el.value == val)[0].placeholder;
         }
      }
   }

</script>

<style lang="scss">
   @import "@/style/_variables.scss";

   .el-card.search {
      padding: 0;
      
      
      .el-input-group__append, 
      .el-input-group__prepend,
      .el-input__inner:not(:focus) {
         border-color: $border-color;
      }

      .el-input-group__prepend {
         border-top-left-radius: $radius;
         border-bottom-left-radius: $radius;
         width: 200px;

         .el-select {
            // width: 100%;
         }

         @media (max-width: 768px) {
            width: 30%;
         }
      }

      .el-input-group__append {
         border-top-right-radius: $radius;
         border-bottom-right-radius: $radius;
      }
   }

   .el-select-dropdown.el-popper {
      border-radius: $radius;

      .el-select-dropdown__list {
         padding: 8px 0;
      }
   }

</style>
