import api from '@/services/api';

export default {
   fetchGet(path, data) {
      return api().get(path, data)
   },
   fetchPost(path, data) {
      return api().post(path, data)
   },
   findPresents(data) {
      return api().post('api/find_presents', data, {
         'Content-Type': 'application/x-www-form-urlencoded'
      });
   }
}